// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------


// Affix header
.navbar-fixed-bottom, .navbar-fixed-top{
	.navbar-collapse{
		max-height: none;
	}
}

// Top navbar
.top-navbar{
}
// End Top navbar

// Navbar default
.navbar-default{
	background-color: transparent;
	border-color: inherit;
	border: none;
	border-radius: 0px;
	margin-bottom: 0px;
	min-height: 0px;

	// Navbar header
	.navbar-header{
		background-color: $white;
		height: auto;

		// Navbar brand
		.navbar-brand{
			max-width: 200px;
			height: auto;
			padding: 0px;
			margin-right: 15px;
			margin-left: 15px;
			margin-top: 15px;
			margin-bottom: 15px;

			img{
				width: 100%;
			}
		}
		// End Navbar brand
	}
	// End Navbar header

	// Navbar toggle
	.navbar-toggle{
		-webkit-transition: all 0.3s ease;
		transition: all 0.3s ease;
		background-color: transparent;
		margin: 0px;
		top: 16px;
		right: 15px;
		border: none;
		padding: 9px 0px;

		@include on-event{
			background-color: transparent;
		}

		// Navbar toggle initial
		&.collapsed{
			background-color: transparent;

			.icon-bar{
				background-color: $primary-color;

				&.top-bar{
					-webkit-transform: rotate(0);
					transform: rotate(0);
				}

				&.middle-bar{
					opacity: 1;
				}

				&.bottom-bar{
					-webkit-transform: rotate(0);
					transform: rotate(0);
				}
			}
		}
		// End Navbar toggle initial

		// Navbar toggle opened
		.icon-bar{
			-webkit-transition: all 0.3s ease;
			transition: all 0.3s ease;
			background-color: $primary-color;
			height: 3px;
			width: 25px;

			// Transform [X]
			&.top-bar{
				-webkit-transform: rotate(45deg);
				transform: rotate(45deg);
				-webkit-transform-origin: 10% 10%;
				transform-origin: 10% 10%;
			}

			&.middle-bar{
				opacity: 0;
			}

			&.bottom-bar{
				-webkit-transform: rotate(-45deg);
				transform: rotate(-45deg);
				-webkit-transform-origin: 10% 90%;
				transform-origin: 10% 90%;
			}
			// End Transform [X]
		}
		// End Navbar toggle opened
	}
	// End Navbar toggle

	// Navbar collapse -> Mobile menu dropdown
	.navbar-collapse{
		-webkit-box-shadow: none;
		box-shadow: none;
		border: none;
		background-color: $primary-color;

		// *** External Vendor Design ***
		// width: 230px;
		// position: absolute;
		// right: 0px;
		// left: auto;
		// z-index: 1;
		// -webkit-box-shadow: -8px 8px 15px -5px rgba(0,0,0,0.15);
		// box-shadow: -8px 8px 15px -5px rgba(0,0,0,0.15);
		// margin: 0px;
		// *** End External Vendor Design ***
	}
	// End Navbar collapse -> Mobile menu dropdown

	// Navbar nav
	ul.navbar-nav{
		margin-top: 0px;
		margin-bottom: 0px;
		text-align: center;
		background-color: transparent;

		// Navigation items
		li a, .open ul.dropdown-menu>li>a, ul.dropdown-menu>li>a{
			-webkit-transition: all 0.3s ease;
			transition: all 0.3s ease;
			background-color: transparent;
			font-size: 18px;
			// font-weight: bold;
			color: $white;
			padding-top: 5px;
			padding-bottom: 5px;
			text-transform: none;
			word-break: break-word;
			white-space: normal;
			line-height: 1.3;

			font-family: $secondary-font;
			font-weight: bold;


			@include on-event{
				background-color: $secondary-color;
				color: $text-color;
				text-decoration: none;
			}
		}
		// End Navigation items

		// Activated item
		li.active>a{
			background-color: $secondary-color;
			color: $text-color;

			@include on-event{
				background-color: shade($secondary-color, 15%);
				color: $text-color;
			}
		}
		// End Activated item

		// Dropdown parent item
		li.dropdown{
			// Dropdown hover
			@include on-event{
			}

			// Dropdown icon ▼
			i.fa.fa-angle-down{
				display: none;
				padding-left: 5px;

				&:before{
					content: "\f107";
				}
			}
			// Dropdown icon ▼

			// Dropdown menu open
			&.open{
				// Parent item
				>a{
					background-color: $hover-color;
					color: $white;
					border-color: transparent;
				}
				// End Parent item
			}
			// End Dropdown menu open

			// Dropdown menu open
			&.open, &{
				// Navbar dropdown menu
				ul.dropdown-menu{
					background-color: shade($primary-color, 15%);
					text-align: center;
					padding-top: 0px;
					padding-bottom: 0px;
					border-radius: 0px;

					// Display dropdown-menu -> Only mobile
					display: block;
					position: relative;
					width: 100%;
					-webkit-box-shadow: none;
					box-shadow: none;
					border: none;
					// End Display dropdown-menu -> Only mobile

					// List item
					>li{
						// Dropdown menu items
						>a{
							color: $white;

							@include on-event{
								background-color: $secondary-color;
								color:  $text-color;
							}
						}
						// End Dropdown menu items

						// Activated dropdown item
						&.active>a{
							background-color: $secondary-color;
							color: $text-color;

							@include on-event{
								background-color: shade($secondary-color, 15%);
								color: $text-color;
							}
						}
						// End Activated dropdown item
					}
					// End List item
				}
				// End Navbar dropdown menu
			}
			// Dropdown menu open
		}
		// End Dropdown parent item
	}
	// Navbar nav
}
// End Navbar default

// Search mobile button
.search-toggle-button-mobile{
	// Search mobile button closed
	&.collapsed{
		color: $primary-color;
	}
	// End Search mobile button closed

	color: $primary-color;
	font-size: 18px;
	font-weight: bold;
	width: inherit;
	border-radius: 0px;
	margin: 0px;
	position: absolute;
	top: 12px;
