@media (min-width: 768px){

	// Navbar default
	.navbar-default{
		background-color: transparent;
		height: auto;
		padding-top: 0px;
		padding-bottom: 0px;

		.join-top{
			padding-left: 20px;
		}

		// Navbar header
		.navbar-header{
			background-color: transparent;
			float: left;

			// Navbar brand
			.navbar-brand{
				max-width: none;
				margin: 0px;
			}
			// End Navbar brand
		}
		// End Navbar header

		// Navbar collapse -> Mobile menu dropdown
		.navbar-collapse{
			background-color: $white;
			padding-left: 0px;
			padding-right: 0px;
			padding-top: 0px;

			// *** External Vendor Design ***
			// width: auto;
			// position: static;
			// left: auto;
			// right: auto;
			// -webkit-box-shadow: none;
			// box-shadow: none;
			// *** End External Vendor Design ***
		}
		// End Navbar collapse -> Mobile menu dropdown

		// Navbar nav
		ul.navbar-nav{
			@include flex-navigation;

			li{
				@include flex-child;
			}

			padding-top: 0px;
			padding-bottom: 0px;
			margin: 0 auto;
			float: right;
			// width: 100%;

			// Navigation items
			li a, .open ul.dropdown-menu>li>a, ul.dropdown-menu>li>a{
				font-size: 14px;
				color: $text-color;
				word-break: normal;
				white-space: nowrap;
				padding-top: 10px;
				padding-bottom: 10px;

				@include on-event{
					background-color: transparent;
					color: $hover-color;
					text-decoration: none;
				}
			}
			// End Navigation items

			// Activated item
			li.active>a{
				background-color: transparent;
				color: $black;

				@include on-event{
					background-color: transparent;
					color: shade($hover-color, 15%);
				}
			}
			// End Activated item

			// Dropdown parent item
			li.dropdown{
				padding-bottom: 20px;

				// Dropdown hover
				&.open, &{
					&:hover{
						ul.dropdown-menu{
							display: block;
						}
					}
				}
				// End Dropdown hover

				// Dropdown icon ▼
				i.fa.fa-angle-down{
					display: inline-block;
					padding-left: 2px;

					&:before{
					}
				}
				// End Dropdown icon ▼

				// Dropdown menu open
				&.open{
					// Parent item
					>a{
						background-color: transparent;
						color: $hover-color;
					}
					// End Parent item
				}
				// End Dropdown menu open

				// Dropdown menu open
				&.open, &{
					// Navbar dropdown menu
					ul.dropdown-menu{
						background-color: $white;
						text-align: left;
						border: none;
						border-radius: 0px;
						width: 100%;
						margin-top: 0px;

						// Display dropdown-menu -> Only mobile
						display: none;
						position: absolute;
						-webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
						box-shadow: 0 6px 12px rgba(0,0,0,.175);
						// End Display dropdown-menu -> Only mobile

						// List item
						>li{
							border: none;
							border-bottom: 2px solid $third-color;

							&:last-child{
								border-bottom: none;
							}

							// Dropdown menu items
							>a{
								color: $black;
								word-break: break-word;
								white-space: normal;
								display: block;
								border-left: 6px solid transparent;
								padding-left: 10px;

								@include on-event{
									background-color: $third-color;
									border-left-color: $primary-color;
								}
							}
							// End Dropdown menu items

							// Activated dropdown item
							&.active>a{
								background-color: $third-color;
								border-left-color: $primary-color;

								@include on-event{
									background-color: shade($third-color, 15%);
									border-left-color: shade($primary-color, 15%);
								}
							}
							// End Activated dropdown item
						}
						// End List item
					}
					// End Navbar dropdown menu
				}
				// Dropdown menu open
			}
			// End Dropdown parent item
		}
		// Navbar nav
	}
	// End Navbar default

}

